import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'rutChile'
})
export class RutChilePipe implements PipeTransform {

  transform(value: any, args?: any): any {

    var initalValue = value;
    initalValue = initalValue.replace(/[^0-9kK]/g,'');
    return "$"+initalValue;
  }

}
