import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthService } from './servicios/secure/auth.service';
import { NoserviceComponent } from './componentes/noService/noservice.component';
import { TokenerrorComponent } from './componentes/tokenError/tokenerror.component';
import { MasterPageComponent } from './componentes/masterPage/masterpage.component';

const routes: Routes = [
  // {path: '', component: AppComponent, canActivate: [AuthService] },
  { path: '', component: MasterPageComponent,canActivate: [AuthService] },
	{ path: 'noservice', component: NoserviceComponent },
	{ path: 'tokenerror', component: TokenerrorComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
