import { IresponseWeb } from '../../model/tokenvalidator/IresponseWeb.model';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

import { constants } from '../../utils/constants';
import { environment } from '../../../environments/environment';

@Injectable()
export class WstokenService {

  private wstokenVlidation = environment.wsvalidatoken;

  private wstokenDecoder = environment.wstokenDecoder;
  private wsGenerateToken = environment.wsGenerateToken;
  private wstokenPP3015 = environment.wsvalidatokenOag;
  private wstokenGeneraPublico =environment.wsGeneraTokenPublico;

  constructor(private http: HttpClient) { }

  public TokenValidator(encodedData: string,dispositivo:string, plataforma:string, app:string,sistemaOperativo:string) {
    const headers = new Headers({ Accept: 'application/json' });
    headers.append('Access-Control-Allow-Origin', '*');
    headers.append('Content-Type', 'application/json');
    //const options = new RequestOptions({ headers });
    const body = JSON.parse('{ "dispositivo" : "' + dispositivo + '","encodedData": "' + encodedData + '","plataforma": "' +plataforma+ '","app": "' +app+'","sistemaOperativo": "' +sistemaOperativo+'"}');
    const url = `${this.wstokenPP3015}`;
    return this.http.post<any>(url, body);
  }


  public TokenDecoder(encodedData: string): Observable<IresponseWeb> {
    const headers = new Headers({ Accept: 'application/json' });
    //const options = new RequestOptions({ headers });
    const body = JSON.parse('{ "encodedData" : "' + encodedData  + '"}');
    const url = `${this.wstokenDecoder}`;
    return this.http.post<IresponseWeb>(url, body);
  }

  // tslint:disable-next-line:max-line-length
  public generateToken(rutCliente: string, rutUsuario: string, dispositivo: string, idAplicacion: string, sistemaOperacion: string, plataforma: string): Observable<IresponseWeb> {
    const headers = new Headers({ Accept: 'application/json' });
    //const options = new RequestOptions({ headers });
    const body = JSON.parse('{ "rutCliente" : "' + rutCliente + '",' +
    '"rutUsuario" : "' + rutUsuario + '",' +
    '"dispositivo" : "' + dispositivo + '",' +
    '"idAplicacion" : "' + idAplicacion + '",' +
    '"sistemaOperativo" : "' + sistemaOperacion + '",' +
    '"plataforma" : "' + plataforma + '"}' );
    const url = `${this.wsGenerateToken}`;
    return this.http.post<IresponseWeb>(url, body);
  }

  public GeneraTokenPublico(tipoCred:string){
    const headers = new Headers({ Accept: 'application/json' });
    headers.append('Access-Control-Allow-Origin', '*');
    headers.append('Content-Type', 'application/json');
    const body = JSON.parse('{ "tipoCred" : "'+tipoCred+'"}');
    const url = `${this.wstokenGeneraPublico}`;
    return this.http.post<any>(url, body);
    }

}
