  public requestDataFromMultipleSources(): Observable<any[]> {
    let response1 = this.simulacionService.obtieneTasas();
    let response2 = this.simulacionService.obtieneValorNotario();
    let response3 = this.simulacionService.obtieneValorPrenda();
    let response4 = this.simulacionService.obtieneValorDesgravamen();
    let response5 = this.simulacionService.obtieneValorCesantia();
    let response6 = this.simulacionService.obtieneValorImpuesto();
    let response7 = this.simulacionService.obtieneValorFactor();
    let response8 = this.simulacionService.obtieneValorPreguntas();
    return forkJoin([response1, response2, response3, response4, response5, response6, response7, response8]);
  }


