  private eliminarCaracteresIzquierdaRut(rut: string, caracter: string): string {
    if (rut === null) { return ''; }
    const aux = '';
    let cont = 0;
    for (const entry of rut) {
        if (entry === caracter) {
            cont++;
        } else {
            break;
        }
    }
    return rut.substring(cont, rut.length);
  }
